<template>
	<h1 class="text-center mb-4">Works</h1>
	<div class="row">
		<div 
		class="col-6 col-md-4 col-lg-3 ov-relative mb-3"  
		v-for="(obj,index) in workData" 
		:key="obj.id"
		:i="index"
		:class="refInd !== index && refBool ? 'disable' : ''"
		>

			<proj 
			:obj="obj" 
			v-on:disable-tog="disTog"
			/>

		</div>

	</div>
</template>

<script>
	import portData from '../assets/data/portData.json';
	import proj from './reusable/workscon-proj.vue';
	export default{
		name: 'workscon',
		components:{
			proj
		},
		data(){
			return{
				workData : portData,
				refInd: null,
				refBool: false,
			}
		},
		methods: {
			disTog: function(i,bool){
				this.refInd = i;
				this.refBool = bool;
			}
		}
	}

</script>