<template>
	<agile
	ref="main"
	v-if="imgarr.length > 1"
	:navButtons="false" 
	:infinite="true" 
	:center-mode="true" 
	:dots="false"
	class="mb-3 main-slide" 
	@after-change="$refs.sub.goTo(slideChange($event));"
	>
		<div class="slide slide-img" v-for="(isrc) in imgarr" :key="isrc" :ind="index">
			<img :src="rendImg(isrc.img)" alt="">
		</div>
	</agile>

	<agile 
	ref="sub"
	v-if="imgarr.length > 1"
	:navButtons="false" 
	:infinite="true" 
	:centerMode="true" 
	:slides-to-show="3" 
	class="subslide"
	@after-change="$refs.main.goTo(slideChange($event));"

	>
		<div
		refs="subimg"
		class="slide slide-img mb-3" 
		v-for="(isrc, index) in imgarr" 
		:key="isrc" 
		@click="$refs.sub.goTo(index)"
		>
			<img :src="rendImg(isrc.img)" alt="">
		</div>
	</agile> 

	<div v-if="imgarr.length == 1">
		<img :src="rendImg(imgarr[0].img)" alt="">
	</div>
</template>

<script>
	import { VueAgile } from 'vue-agile';
	export default{
		name: 'workscon-carousel',
		props: {
			imgarr: Object
		},
		components: {
			agile: VueAgile 
		},
		methods:{
			rendImg: function(str){
				return require('./../../assets/img/'+str);
			},
			slideChange (event) {
				return event.currentSlide;
			},
			test: function(){
				console.log("test");
			}
		}
	}
</script>