<template>
	<div class="project">
		<div class="thumb" @click="conAct" :class="{'no-shade': slTog}">
			<img :src="rendImg(obj.imgsrc[0].img)" class="thb-img">
			<h4 class="title text-center" :class="{'d-none':slTog}">{{obj.header}}</h4>
		</div>
	</div>
	<div class="slideout" :class="{'act':slTog}">
		<button class="btn close" @click="conAct"><svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" data-v-5e628c52=""><path d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z" data-v-5e628c52=""></path></svg>
		</button>
		<div class="container">
			<div class="row align-items-center">
				<div class="carousel col-12 col-md-6 mb-4 mb-md-0">
					<!-- to insert the carousel component here -->
					<carousel :imgarr="obj.imgsrc"/>
				</div>
				<div class="p-info col-12 col-md-6">
					<h5 class="title">{{obj.header}}</h5>
					<a v-if="obj.exlink !== '' " :href="obj.exlink" target="_blank">View live work</a>
					<div class="desc" v-html="obj.content">
					</div>
				</div>
			</div>
			
			
		</div>
		
	</div>

</template>

<script>
	import carousel from "./workscon-carousel.vue";
	export default{
		name: 'workscon-proj',
		components:{
			carousel
		},
		props: {
			obj: Object,
			switchSlide: Boolean
		},
		data(){
			return{
				slTog: false,
			}
		},
		watch: {
			switchSlide: function(){
				// console.log(this.switchSlide);
			}
		},
		methods:{
			conAct: function(){
				this.slTog = !this.slTog;
				var indChild = this.obj.id;
				var chTog = this.slTog;
				this.$emit('disable-tog', indChild, chTog);
			},
			rendImg: function(str){
				return require('./../../assets/img/'+str);
			}
		}
	}
</script>